import { Utils } from "hornet-js-utils";
import { Logger } from "hornet-js-utils/src/logger";

import { AbstractRoutes, PageRouteInfos } from "hornet-js-core/src/routes/abstract-routes";
import { Roles } from "applitutoriel-js-common/src/utils/roles";
import { RecherchePartenairesPage } from "applitutoriel-js-common/src/views/par/par-rpa-page";
import { FichePartenairePage, PAR_MODE_CREER } from "applitutoriel-js-common/src/views/par/par-fpa/fiche-partenaire-page";
import {
    URL_PAR_CREER,
    URL_PAR_RECHERCHER
} from "applitutoriel-js-common/src/utils/urls";
import { FichePartenaireServiceImpl } from "src/services/page/par/par-fpa-service-page-impl";
import { RecherchePartenaireServiceImpl } from "src/services/page/par/par-rpa-service-page-impl";

const logger: Logger = Utils.getLogger("applitutoriel.routes.par.par-routes");

export default class PartenairesRoutesClient extends AbstractRoutes {
    constructor() {
        super();

        this.addPageRoute("/(\\w+)/(\\d+)",
            (mode, id) => new PageRouteInfos(FichePartenairePage, {mode: mode, id:id}, FichePartenaireServiceImpl),
            Roles.ADMIN
        );

        /* Routes des pages */

        /* Nouvelle page de recherche avec critres par dfaut */
        this.addPageRoute("/",
            () => new PageRouteInfos(RecherchePartenairesPage, {reset: true}, RecherchePartenaireServiceImpl),
            Roles.EVERYONE
        );

        /* Page de recherche avec critres en session */
        this.addPageRoute(URL_PAR_RECHERCHER,
            () => new PageRouteInfos(RecherchePartenairesPage, {reset: false}, RecherchePartenaireServiceImpl),
            Roles.EVERYONE
        );

        /* Page de cration de partenaire */
        this.addPageRoute(URL_PAR_CREER,
            () => new PageRouteInfos(FichePartenairePage, {mode: PAR_MODE_CREER}, FichePartenaireServiceImpl),
            Roles.ADMIN
        );
    }
}



