/**
 * hornet-js-utils - Partie commune et utilitaire  tous les composants hornet-js
 *
 * @author MEAE - Ministre de l'Europe et des Affaires trangres
 * @version v5.1.0
 * @link git+https://github.com/diplomatiegouvfr/hornet-js.git
 * @license CECILL-2.1
 */

/**
 * Classe utilitaire sur les objets.
 */
export class ObjectUtils {

    /**
     * recupre un sous objet d'un objet (deep attribute)
     * @example
     * let object1 = { id : 1, desc : { type : {id : 2, label : 'label'}}
     * ObjectUtils.getSubObject(object1, "desc.type") => {id : 2, label : 'label'}
     * @param {Object} obj objet  parser
     * @param {string} chainage d'attribut attr1.attr2....attrn
     * @return la valeur de l'attribut attrn de l'objet.
     *
     */
    static getSubObject(obj: any, deepAttr: string) {
        let res: any;
        if (obj && deepAttr) {
            let stringKeys: Array<String> = deepAttr.split(".");
            let value = obj;
            for (let attr in stringKeys) {
                let key = stringKeys[attr];
                if (key) {
                    value = value[key as string];
                    if (!value) {
                        break;
                    }
                }
            }
            res = value;
        }
        return res;
    }
}



