/**
 * Contient les informations relatives  un pays
 */
import Bean from "hornet-js-bean/src/decorators/Bean";
import Map from "hornet-js-bean/src/decorators/Map";

export type Pays = {
    /** Identifiant du pays */
    id: number,

    /** Libell du pays */
    libelle: string,

    /** Libell de la nationalit */
    nationalite: string
};

@Bean
export class PaysMetier {
    @Map()
    id: number;
    @Map()
    libelle: string;
    @Map()
    nationalite: string;
}



